.project-container {
  display: flex;
  flex-direction: row;
  height: 100%;
}
.project-list {
  position: relative;
  background-color: #3a3b3d;
  padding-bottom: 9999px;
  margin-bottom: -9959px;
  overflow-y: auto;
}
.project-list-item {
  flex-shrink: 0;
  width: 200px;
  background: #fff;
  padding: 14px 20px;
  box-shadow: 0 1px 5px #e5e5e5;
  border-radius: 6px;
  transition: all 0.3s;
  position: relative;
  margin: 10px 16px;
  cursor: pointer;
}
.project-list-item h2 {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-weight: 700;
  font-size: 14px;
}
.project-list-item .project-tit {
  position: relative;
  display: inline-block;
  padding: 10px;
  font-size: 12px;
}
.project-list-item .project-tit::before {
  content: '';
  display: block;
  width: 2px;
  height: 18px;
  background: #f84c5b;
  border-radius: 4px;
  margin-bottom: 3px;
  position: absolute;
  left: 0;
}
.project-list-item p {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding: 5px 10px;
  border: 1px solid #eee;
  border-radius: 4px;
  background: #f5f5f5;
  font-size: 12px;
}
.project-list .delete-icon {
  display: none;
  position: absolute;
  top: 16px;
  right: 16px;
}
.project-list:hover .delete-icon {
  display: block;
}
.project-detail {
  flex-grow: 1;
  padding-top: 10px;
}
.project-detail-header {
  position: sticky;
  top: 0;
  background-color: #fff;
  padding: 30px 0;
  margin: 0 10px;
  border-radius: 6px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: all 0.3s;
  z-index: 9;
}
.project-detail-header .header-info {
  position: relative;
  display: inline-block;
  max-width: 80%;
  padding: 0 20px;
}
.project-detail-header .header-info h2 {
  font-size: 18px;
  font-weight: 700;
}
.project-detail-header .header-info p {
  font-size: 12px;
  color: #9a9ca0;
  font-weight: 400;
}
.project-detail-header .proxy-switch {
  float: right;
  margin-right: 20px;
  text-align: center;
}
.project-detail-content {
  padding: 0 20px;
  max-width: 1020px;
  margin: auto;
}
.project-detail-content .baseInfo {
  position: relative;
  margin: 10px 0;
  font-size: 16px;
  color: #fff;
  background: rgba(255, 156, 10, 0.8);
  border-radius: 4px;
  box-shadow: 0 2px 3px #eee;
  padding: 30px 20px;
  overflow: hidden;
}
.project-detail-content .baseInfo .tag {
  width: 78%;
  font-size: 13px;
  margin-top: 16px;
  padding-right: 10px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 20px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.project-detail-content .baseInfo .tag:first-of-type {
  margin-top: 0;
}
.project-detail-content .baseInfo .tag span {
  padding: 5px 10px;
  background: rgba(0, 0, 0, 0.4);
  display: inline-block;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  margin-right: 10px;
  width: 100px;
  text-align: center;
}
.project-detail-content .baseInfo .opera {
  width: 20.8%;
  padding: 20px;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  box-shadow: 0 0 3px #000;
  text-align: center;
  background: #252d47;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.project-detail-content .proxy-info {
  padding: 20px;
  padding-bottom: 10px;
  margin-bottom: 10px;
  background-color: #fff;
}
.project-detail-content .proxy-info .cookie {
  word-break: break-all;
}
.project-detail-content .api-list {
  background-color: #fff;
}
.project-detail-content .api-list .opera-btn {
  margin-right: 12px;
  cursor: pointer;
}
.project-detail-content .api-list .opera-btn:hover {
  color: #f8a03c;
}
.project-detail .add {
  width: 42px;
  height: 42px;
  border-radius: 50%;
  position: fixed;
  right: 10px;
  bottom: 30px;
  text-align: center;
  color: #fff;
  line-height: 42px;
  font-size: 18px;
  cursor: pointer;
  transition: all 0.3s;
  z-index: 90;
  background: #f8a03c;
  box-shadow: 0 10px 30px rgba(255, 156, 10, 0.4);
}
